"use client";

import { useEffect } from "react";
import { useRouter } from "next/navigation";

export default function RequireAuthClient() {
  const router = useRouter();

  useEffect(() => {
    let mounted = true;
    async function check() {
      try {
        console.log('[RequireAuthClient] mounted - checking token');
        const token = localStorage.getItem('token');
        console.log('[RequireAuthClient] token:', token);

        if (!token) {
          if (mounted) setTimeout(() => router.replace('/login'), 50);
          return;
        }

        // Validar token en servidor
        const res = await fetch('/api/auth/verify', {
          method: 'GET',
          headers: { Authorization: `Bearer ${token}` },
        });

        if (!res.ok) {
          // token inválido -> limpiar y redirigir
          localStorage.removeItem('token');
          localStorage.removeItem('user');
          if (mounted) router.replace('/login');
          return;
        }

        const data = await res.json();
        if (!data.valid) {
          localStorage.removeItem('token');
          localStorage.removeItem('user');
          if (mounted) router.replace('/login');
        }
      } catch (e) {
        // en caso de error de red, no bloquear la UI
        console.error('[RequireAuthClient] verify error', e);
      }
    }

    check();
    return () => { mounted = false; };
  }, [router]);

  return null;
}
